* 
    border: none
    outline: none
    padding: 0
    margin: 0
    font-family: Raleway
    user-select: none
    flex-wrap: wrap

p
    user-select: text

*::-webkit-scrollbar 
    width: 5px

*::-webkit-scrollbar-thumb
    background-color: #797979
    border-radius: 20px
    border: 1px solid #f1f2f3
    width: 2px

html
    scroll-behavior: smooth
 
a
    text-decoration: none


body 
    background: $White

main 
    width: 100%
    min-height:101vh

.scroll 
    height: calc(100vh * 11)
    display: flex 
    flex-direction: column
    flex-wrap: wrap
    justify-content: center

    width: 100%

    .view 
        height: 90vh
        margin: 5vh 0
        width: 90vw